'use client';

import Link from 'next/link';
import { Search, LogOut } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { useAuth } from '@/hooks/useAuth'; 

export default function Navbar() {
    const { user, logout } = useAuth({ middleware: 'auth' });

    return (
        <nav className="sticky top-4 z-50 mx-4 md:mx-12 mt-4 bg-[#1a1a1a] text-white py-3 px-6 md:px-8 rounded-2xl shadow-xl border border-gray-800/50 backdrop-blur-md transition-all duration-300">
            <div className="flex justify-between items-center">
                <div className="text-xl md:text-2xl font-bold tracking-tight">
                    MainDi<span className="text-[#D93F21]">Hatta</span>.id
                </div>
                
                <div className="hidden md:flex items-center gap-8 text-sm font-medium text-gray-300">
                    <Link href="/pelanggan/home" className="text-white hover:text-[#D93F21] transition">Home</Link>
                    <Link href="/pelanggan/dashboard#about" className="hover:text-[#D93F21] transition">About</Link>
                    <Link href="/pelanggan/produk" className="hover:text-[#D93F21] transition">Produk</Link>
                    <Link href="/pelanggan/booking/create" className="hover:text-[#D93F21] transition">Booking</Link>
                    <Link href="/pelanggan/booking/riwayat" className="hover:text-[#D93F21] transition">Riwayat Booking</Link>
                </div>

                <div className="flex items-center gap-4">
                    <div className="relative hidden sm:block">
                        <input 
                            type="text" 
                            placeholder="Search..." 
                            className="bg-white/10 text-white px-4 py-1.5 rounded-full text-sm focus:outline-none border border-transparent focus:border-[#D93F21] w-32 focus:w-56 transition-all placeholder-gray-400"
                        />
                        <Search className="w-4 h-4 absolute right-3 top-2 text-gray-400" />
                    </div>
                    <div className="flex items-center gap-3 pl-4 border-l border-gray-700">
                        <Link href="/profile">
                        <span className="text-sm text-gray-300 hidden md:block">Hi, {user?.name}</span>
                        </Link>
                        <Button variant="ghost" size="icon" onClick={logout} className="text-white hover:bg-white/10 hover:text-red-400 rounded-full">
                            <LogOut className="w-5 h-5" />
                        </Button>
                    </div>
                </div>
            </div>
        </nav>
    );
}